@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap');
body{
  background-color: #121117;
  color: #D0D0D0;
  font-family: "Roboto", serif;
  font-weight: 300;
  font-style: normal;
  font-size: 18px;
}
.main-container{
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
}
.main-container h1 {
  font-size: 24px;
  color: #525059;
}
.input-container {
  background-color: #24232b;
  width: 90%;
  height: 40px;
  padding: 12px;
  border-radius: 2px;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 16px;
}
.input-container input {
  background-color: transparent;
  border: none;
  font-size: 22px;
  font-weight: bold;
  letter-spacing: 2px;
  color: #8A8A8A;
}
.input-container input:focus {
  outline: none; 
  border: none; 
  box-shadow: none; 
}
.copy-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
.copied-message {
  color: #4caf50;
  font-size: 12px;
  margin-left: 8px;
  opacity: 1;
  transition: opacity 0.3s ease;
}
.copy-button.copied .copied-message {
  opacity: 1;
}
.checkbox-container {
  background-color: #24232b;
  width: 90%;
  padding: 12px;
  border-radius: 2px;
  display: flex;
  flex-direction: column;
}
.checkbox-container .password-length{
  display: flex;
  justify-content: flex-start;
}
.password-length label {
  width: 100%; 
}
.password-length span{
  font-weight: bold;
  color: #48E4C2;
  margin-right: 14px;
}
.checkbox-container input {
  margin-bottom: 20px;
}
.checkbox-container button {
  height: 48px;
  background-color: #48E4C2;
  color: black;
  font-weight: bold;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}


